.i{
    display: flex;
    height: 100vh;
}
.i-left{
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: center;
}
.i-right{
    flex: 1;
    position: relative;
}
.i-left-title1{
    font-weight: bold;
}
.i-left-title2{
    font-weight: 100;
    text-align: right;
}
.i-left-wrapper{
    padding: 50px 80px;
}
.i-left-title{
    height: 50px;
    overflow: hidden;
    margin-top: 5px;
    margin-left: 5px;
}
.i-left-title-wrapper{
    height: 100%;
    animation: move 10s ease-in-out infinite alternate;

}
@keyframes move {
    0%{
        transform: translateY(-50px);
    }
    25%{
        transform: translateY(0);
    }
    50%{
        transform: translateY(-150px);
    }
    100%{
        transform: translateY(-200px);
    }
}
.i-left-title-item{
    font-size: 25px;
    height: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    color: rgb(70, 175, 70);
}
.i-descritpion{
    margin-top: 20px;
}
.i-right-img{
    width: 100%;
    height: 100%;
    object-fit: cover;
}
.bg{
    clip-path: polygon(100% 0%, 100% 48%, 100% 100%, 25% 100%, 0% 50%, 25% 0%);
    background-color: aqua;
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    right: 0;
    z-index: -1;
}
.i-scroll{
    width: 30px;
    height: 30px;
    position: absolute;
    bottom: 20px;
}